import Login from "./Components/Login";
import "./App.css";
import Orders from "./Components/Orders";

import { Route, BrowserRouter as Router } from "react-router-dom";
import Products from "./Components/Products";
import Users from "./Components/Users";

function App() {
  return (
    <Router>
      <div className="App">
        <Route exact path="/react_project" component={Login} />
        <Route exact path="/react_project/products" component={Products} />
        <Route exact path="/react_project/users" component={Users} />
        <Route exact path="/react_project/orders" component={Orders} />
      </div>
    </Router>
  );
}

export default App;
