import Topbar from "./Topbar";
import Table from "./Table";
import "./Products.css";
import { useEffect, useState } from "react";

const cols = [
  { field: "firstName", headerName: "First Name", width: 250, editable: true },
  { field: "lastName", headerName: "Last Name", width: 250, editable: true },
  {
    field: "dateOfBirth",
    headerName: "Date of Birth",
    type: "date",
    width: 250,
    editable: true,
  },
  {
    field: "gender",
    headerName: "Gender",
    width: 250,
    editable: true,
  },
  {
    field: "experience",
    headerName: "Experience",
    width: 200,
    editable: true,
  },
];

const Users = () => {
  const [medForVis, setMedForVis] = useState("none");
  let usersData = JSON.parse(window.localStorage.getItem("usersData"));
  if (usersData == null) {
    usersData = [
      {
        id: 0,
        firstName: "null",
        lastName: "null",
        dateOfBirth: "null",
        gender: "null",
        experience: "null",
      },
    ];
  }
  const [dataForDisplay, setDataForDisplay] = useState(usersData);
  return (
    <>
      <Topbar />
      <div className="outer-wrapper">
        <div className="product-header">
          <h1>Sales Executive</h1>
          <button
            onClick={() => {
              setMedForVis("block");
            }}
          >
            Add Executive
          </button>
        </div>
        <div className="inner-wrapper">
          <Table rows={dataForDisplay} cols={cols}></Table>
        </div>
      </div>

      <div className="medicineForm" style={{ display: medForVis }}>
        <form
          onSubmit={(e) => {
            e.preventDefault();
            var data = {
              id: new Date().getTime(),
              firstName: e.target.firstName.value,
              lastName: e.target.lastName.value,
              dateOfBirth: e.target.dateOfBirth.value,
              gender: e.target.gender.value,
              experience: e.target.experience.value,
            };
            let usersData = window.localStorage.getItem("usersData");
            if (usersData === null) {
              usersData = [];
              usersData.push(data);
              window.localStorage.setItem("usersData", JSON.stringify(usersData));
            } else {
              usersData = JSON.parse(window.localStorage.getItem("usersData"));
              usersData.push(data);
              window.localStorage.setItem("usersData", JSON.stringify(usersData));
            }
            setMedForVis("none");
            setDataForDisplay(usersData);
          }}
        >
          <input type="text" name="firstName" required placeholder="First Name"></input>
          <input type="text" name="lastName" required placeholder="Last Name"></input>
          <input type="date" name="dateOfBirth" required placeholder="Date of Birth"></input>
          <input type="text" name="gender" required placeholder="Gender"></input>
          <input type="text" name="experience" required placeholder="Experience"></input>
          <input id="sub" type="submit" value="Save"></input>
        </form>
      </div>
    </>
  );
};

export default Users;
