import { NavLink } from "react-router-dom";
import classes from "./Topbar.module.css";
import { useHistory } from "react-router-dom";
const Topbar = (props) => {
  const history = useHistory();
  const loginrole = window.localStorage.getItem("userType");
  return (
    <>
      <div className={classes.topbarWrapper}>
        <div className={classes.topbarLeft}>
          <h1 className={classes.logo}>PHARMACY</h1>
        </div>
        {loginrole == "storemanager" ? (
          <div className={classes.topbarMid}>
            <NavLink
              className={classes.menuItem}
              to="/react_project/products"
              activeClassName={classes.activeNav}
            >
              {" "}
              Products
            </NavLink>
            <NavLink
              className={classes.menuItem}
              to="/react_project/orders"
              activeClassName={classes.activeNav}
            >
              {" "}
              Orders
            </NavLink>
            <NavLink
              className={classes.menuItem}
              to="/react_project/users"
              activeClassName={classes.activeNav}
            >
              {" "}
              Users
            </NavLink>
          </div>
        ) : (
          <div className={classes.topbarMid}>
            <NavLink
              className={classes.menuItem}
              to="/react_project/orders"
              activeClassName={classes.activeNav}
            >
              {" "}
              Orders
            </NavLink>
          </div>
        )}
        <div className={classes.topbarRight}>
          <button
            onClick={() => {
              history.push("/react_project");
            }}
          >
            Sign Out
          </button>
        </div>
      </div>
    </>
  );
};

export default Topbar;
